/**
 * Imports : React
 */
import React from 'react';

/**
 * Imports : components
 */
import CardStage from '../view/CardStage';
import CardStageMobile from '../view/CardStageMobile';
import CardBonus from '../view/CardBonus';

/**
 * Imports : media
 */
import { RiApps2Fill } from 'react-icons/ri';
import { HiServerStack } from 'react-icons/hi2';


export default function BlockStages() {

    return (
        <section className={
            'mt-[120px] '
            + 'laptop:mt-[265px]'}>

            <h2 className={'mb-12 section-title'}>
                 
            </h2>

            <div className={
                'hidden '
                + 'laptop:grid laptop:grid-cols-3 laptop:gap-y-[20px] laptop:gap-x-[20px] '
                + 'large:gap-x-[40px]'}>
                <CardStage
                    number='1'
                    title='  '
                    description='    .   .' />
                <CardStage
                    number='2'
                    title=' \'
                    description='  \,     .' />
                <CardStage
                    number='3'
                    title=''
                    description='       \.' />
                <CardStage
                    number='4'
                    title=''
                    description='       .' />
                <CardStage
                    number='5'
                    title=''
                    description='          .' />
                <CardStage
                    number='6'
                    title=''
                    description={'   24' + '7,      .'} />
            </div>

            <div className={
                'flex flex-col space-y-[16px] '
                + 'laptop:hidden'}>
                <CardStageMobile
                    number='1'
                    title='  '
                    description='    .   .' />
                <CardStageMobile
                    number='2'
                    title=' \'
                    description='  \,     .' />
                <CardStageMobile
                    number='3'
                    title=''
                    description='       \.' />
                <CardStageMobile
                    number='4'
                    title=''
                    description='       .' />
                <CardStageMobile
                    number='5'
                    title=''
                    description='          .' />
                <CardStageMobile
                    number='6'
                    title=''
                    description={'   24' + '7,      .'} />
            </div>

            <div className={'flex flex-col mt-[90px] large:mt-[180px] space-y-[18px] '
                + 'laptop:flex-row laptop:justify-between laptop:space-y-0 laptop:space-x-[95px]'}>

                <CardBonus
                    title=' '
                    description='    ,            .   -     .    -          '
                    icon={<RiApps2Fill className='text-sky-500 w-14 h-14 absolute top-10 right-10' />} />

                <CardBonus
                    title=' '
                    description='      .     ,       ,           .'
                    icon={<HiServerStack className='text-sky-500 w-14 h-14 absolute top-10 right-10' />} />

            </div>

        </section>
    )
}

