/**
 * Imports : React
 */
import React from "react";

/**
 * Imports : Swiper
 */
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, EffectCoverflow } from 'swiper';
import 'swiper/css';
import 'swiper/css/pagination';

/**
 * Imports : components
 */
import CardWebApp from "./CardWebApp";

/**
 * Imports : media
 */
import QrBusiness from '../../../assets/telegram/qr_business.jpg';
import ScreenshotBusiness from '../../../assets/telegram/screenshot_web-app_business.png';
import QrCoffee from '../../../assets/telegram/qr_coffee.jpg';
import ScreenshotCoffee from '../../../assets/telegram/screenshot_web-app_food.png';
import QrEcom from '../../../assets/telegram/qr_ecom.jpg';
import ScreenshotEcom from '../../../assets/telegram/screenshot_web-app_ecom.png';
import QrService from '../../../assets/telegram/qr_service.jpg';
import ScreenshotService from '../../../assets/telegram/screenshot_web-app_service.png';
import QrEducation from '../../../assets/telegram/qr_education.jpg';
import ScreenshotEducation from '../../../assets/telegram/screenshot_web-app_education.png';



export default function Gallery() {

  const pagination = {
    clickable: true,
    renderBullet: function (index, className) {
      return '<span class="w-8 h-8 bg-sky-500 rounded-[5px] ' + className + '"></span>';
    },
  };

  return (
    <Swiper
      className={'hidden laptop:block'}
      effect={"coverflow"}
      autoHeight={true}
      centeredSlides={true}
      slidesPerView={"auto"}
      coverflowEffect={{
        rotate: 50,
        stretch: 700,
        depth: 300,
        modifier: 1,
        slideShadows: false,
      }}
      pagination={pagination}
      modules={[EffectCoverflow, Pagination]}
    >
      <SwiperSlide>
        <CardWebApp
          title=' '
          description='     Telegram Web Apps   . 
             ,      ,         , 
              Telegram     . 
                  TWA,         .'
          qr={QrService}
          image={ScreenshotService}
          link='https://t.me/its_demo_services_bot' />
      </SwiperSlide>
      <SwiperSlide>
        <CardWebApp
          title='E-commerce'
          description='     Telegram Web Apps   E-Commerce. 
                E-Commerce  Telegram,      CRM . 
                  TWA,         .'          
          qr={QrEcom}
          image={ScreenshotEcom}
          link='https://t.me/its_demo_shop_bot' />     
      </SwiperSlide>
      <SwiperSlide>
        <CardWebApp
          title=' '
          description='     Telegram Web Apps    . 
              ,   ,      . 
                 .'             
          qr={QrBusiness}
          image={ScreenshotBusiness}
          link='https://t.me/its_demo_route_bot' /> 
      </SwiperSlide>
      <SwiperSlide>
        <CardWebApp
          title='Food tech'
          description='     Telegram Web Apps   Food tech. 
            ,                   . 
          Food tech     . '    
          qr={QrCoffee}
          image={ScreenshotCoffee}
          link='https://t.me/its_demo_food_bot' />      
        </SwiperSlide>
        <SwiperSlide>
        <CardWebApp
          title=' '
          description='  Telegram Web Apps         . 
                   .'
          qr={QrEducation}
          image={ScreenshotEducation}
          link='https://t.me/its_demo_education_bot' />      
        </SwiperSlide>

    </Swiper>
  );
}